
.cellNowRap {
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.isSelected {
  background-color: #99CCFF;
}
.isStatus {
  background-color: #DADEE2 !important;
}
.wf-color .el-color-picker__trigger {
  height: 32px !important;
}
.isOperable {
  cursor: pointer;
}
.wf-text {
  font-size: 14px;
  text-align: right;
  line-height: 32px;
}
.wf-tag:hover {
  -webkit-transition: border linear 0.2s, -webkit-box-shadow linear 0.5s;
  border-color: rgba(255, 255, 0, 1);
  -webkit-box-shadow: 0 0 18px rgba(255, 255, 0, 1);
}
.wf-triangle {
  width: 10px;
  height: 10px;
  border-top: 10px solid red;
  border-left: 10px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}
.wf-triangle_lv {
  width: 13px;
  height: 13px;
  border-top: 13px solid red;
  border-left: 13px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}
.wf-triangle_ot {
  width: 13px;
  height: 13px;
  border-top: 13px solid blue;
  border-left: 13px solid transparent;
  position: absolute;
  right: 0;
  top: 0;
}
.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}



