
.box {
  width: 400px;
}
.box .top {
    text-align: center;
}
.box .left {
    float: left;
    width: 60px;
}
.box .right {
    float: right;
    width: 60px;
}
.box .bottom {
    clear: both;
    text-align: center;
}
.box .item {
    margin: 4px;
}
.box .left .el-tooltip__popper,
  .box .right .el-tooltip__popper {
    padding: 8px 10px;
}



