
.myrow_read {
  background: #Fff;
}
.myrow_read .textinfo {
    display: block;
}
.myrow_read .editinfo {
    display: none;
}
.myrow_edit {
  background: #f0f9eb;
}
.myrow_edit .textinfo {
    display: none;
}
.myrow_edit .editinfo {
    display: block;
}
.el-table .warning-row {
  background: oldlace;
}
.el-table .success-row {
  background: #f0f9eb;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}



