
.basicInfo {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
  padding-bottom: 15px;
}
.tabtitle {
  /*        height: 100%;*/
  width: 100%;
  min-height: 20px;
}
.tabtitle h5 {
    border-left: 2px solid #88b7e0;
    text-indent: 8px;
}
.tabtitle .division {
    margin-top: 5px;
    width: 100%;
    border: 1px solid #d2d1d1;
}
.tabtitle .infoborder {
    margin-top: 5px;
    width: 100%;
    border: 2px solid #d2d1d1;
}
.row {
  margin: 0px 0px 20px 40px;
  font-size: 12px;
}



