.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table .saved {
  background: #ff9933;
}
.el-table .confirmed {
  background: #cccccc;
}
.el-table .others {
  background: #ffffff;
}

/* .el-table .exception {
  background:#ff6666
} */
.usercode-span {
  display: inline-block;
  width: 18px;
  height: 1em;
}
.overpoint {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.bottom .save {
    -ms-flex-item-align: end;
        align-self: flex-end;
}
.clock-dialog {
  height: 70vh;
}
.clock-dialog .el-dialog__body {
    height: 65vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow-y: hidden !important;
}
.clock-dialog .scroll-view {
    -ms-flex: 1;
        flex: 1;
}
.clock-dialog .bottom {
    margin-top: -8px;
    -ms-flex: none;
        flex: none;
}
.clock-dialog .tb-delete {
    position: relative;
}
.clock-dialog .tb-delete::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      z-index: 100;
      height: 1px;
      width: 100%;
      box-sizing: content-box;
      border-top: 1px solid red;
      opacity: 0.8;
}
.clock-dialog .sign-color {
    background-color: #62da62;
}
.text-align-left /deep/ .el-form-item__label {
  text-align: left;
}
.el_datetime .el-input__inner {
  padding: 0 25px !important;
}
.el-table input {
  padding: 0 30px !important;
}



