/*滚动条整体部分,必须要设置*/
.mine-scrollbar::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}
.mine-scrollbar::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 5px;
}
.mine-scrollbar::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 5px;
}
.mine-scrollbar::-webkit-scrollbar-thumb:horizontal {
  width: 3px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 3px;
}
.mine-scrollbar::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.mine-scrollbar::-webkit-scrollbar-thumb {
  background-color: #a1a3a9;
  border-radius: 3px;
}

